import React from "react";
import NavBar from "./NavBar";
import { Navigate, Outlet } from "react-router-dom";
import { validateToken } from "../../AuthService";

const RootElement = () => {
  const token = validateToken();
  return token ? (
    <main>
      <NavBar />
      <Outlet />
    </main>
  ) : (
    <Navigate to="/" />
  );
};

export default RootElement;
