import React, { useEffect, useState } from "react";
import { IoRestaurant } from "react-icons/io5";
import { IoHome } from "react-icons/io5";
import { TbFileInvoice } from "react-icons/tb";
import Avtar from "../../assets/profile.png";
import { NavLink, useNavigate } from "react-router-dom";
import { IoIosArrowDown } from "react-icons/io";
import { useDispatch, useSelector } from "react-redux";
import { authState, logout, reStore } from "../../redux/slices/AuthSlice";

const NavBar = () => {
  // const [currentDate, setCurrentDate] = useState(new Date());
  const [menu, setmenu] = useState(false);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { userData } = useSelector(authState);
  const intitalCalls = async () => {
    await dispatch(reStore());
  };

  useEffect(() => {
    intitalCalls();
  }, []);
  // useEffect(() => {
  //   // const interval = setInterval(() => {
  //   //   setCurrentDate(new Date());
  //   // }, 1000);
  //   // return () => clearInterval(interval);
  // }, []);

  const loginHandler = () => {
    dispatch(logout());
    navigate("/");
  };
  return (
    <>
      <div className=" border-b border-slate-300">
        <nav className="text-slate-500  mx-auto flex justify-between px-8 py-3">
          <div className="logo cursor-pointer">
            <span className=" inline-flex  gap-1 text-orange-500 font-semibold text-2xl">
              <IoRestaurant />
              COCO
            </span>
          </div>
          <div className=" hidden md:flex justify-around gap-x-10 items-center">
            <ul className="flex justify-around gap-x-10 items-center">
              <li>
                <NavLink
                  to="/home"
                  className={({ isActive }) =>
                    isActive
                      ? "text-orange-500 inline-flex gap-1 items-center "
                      : "hover:text-orange-500 inline-flex gap-1 items-center"
                  }
                  end
                >
                  <i>
                    <IoHome />
                  </i>
                  Home
                </NavLink>
              </li>
              {(userData[0]?.role === "Super admin" ||
                userData[0]?.role === "admin") && (
                <li>
                  <NavLink
                    to="../home/Order"
                    className={({ isActive }) =>
                      isActive
                        ? "text-orange-500 inline-flex gap-1 items-center "
                        : "hover:text-orange-500 inline-flex gap-1 items-center"
                    }
                  >
                    <i>
                      <TbFileInvoice />
                    </i>
                    Order
                  </NavLink>
                </li>
              )}

              {userData[0]?.role === "Super admin" && (
                <li className=" relative cursor-pointer">
                  <div
                    className=" inline-flex items-center gap-2"
                    onClick={() => setmenu(!menu)}
                  >
                    <span>Chef</span>
                    <span>
                      <IoIosArrowDown />
                    </span>
                  </div>
                  <ul
                    className={
                      menu
                        ? " bg-white absolute top-11 w-32 rounded-md py-2 text-start px-3 shadow-lg"
                        : "hidden"
                    }
                  >
                    <li className="border-b border-slate-300 py-1 hover:border-orange-500">
                      <NavLink
                        to="/Master/TG"
                        className={({ isActive }) =>
                          isActive
                            ? "text-orange-500  "
                            : "hover:text-orange-500 "
                        }
                      >
                        Table Group
                      </NavLink>
                    </li>
                    <li className="border-b border-slate-300 py-1 hover:border-orange-500">
                      <NavLink
                        to="/Master/Table"
                        className={({ isActive }) =>
                          isActive
                            ? "text-orange-500  "
                            : "hover:text-orange-500 "
                        }
                      >
                        Table
                      </NavLink>
                    </li>
                    <li className="border-b border-slate-300 py-1 hover:border-orange-500">
                      <NavLink
                        to="/Master/Category"
                        className={({ isActive }) =>
                          isActive
                            ? "text-orange-500  "
                            : "hover:text-orange-500 "
                        }
                      >
                        Category
                      </NavLink>
                    </li>
                    <li className="border-b border-slate-300 py-1 hover:border-orange-500">
                      <NavLink
                        to="/Master/Group"
                        className={({ isActive }) =>
                          isActive
                            ? "text-orange-500  "
                            : "hover:text-orange-500 "
                        }
                      >
                        Group
                      </NavLink>
                    </li>
                    <li className="border-b border-slate-300 py-1 hover:border-orange-500">
                      <NavLink
                        to="/Master/Item"
                        className={({ isActive }) =>
                          isActive
                            ? "text-orange-500  "
                            : "hover:text-orange-500 "
                        }
                      >
                        Item
                      </NavLink>
                    </li>
                    <li className="border-b border-slate-300 py-1 hover:border-orange-500">
                      <NavLink
                        to="/Master/Role"
                        className={({ isActive }) =>
                          isActive
                            ? "text-orange-500  "
                            : "hover:text-orange-500 "
                        }
                      >
                        Role
                      </NavLink>
                    </li>
                    <li className=" py-1 hover:border-orange-500">
                      <NavLink
                        to="/Master/User"
                        className={({ isActive }) =>
                          isActive
                            ? "text-orange-500  "
                            : "hover:text-orange-500 "
                        }
                      >
                        User
                      </NavLink>
                    </li>
                  </ul>
                </li>
              )}
            </ul>
            <div className="rounded-full bg-orange-100 px-5 text-center text-orange-500">
              <span className=" p-0">Dine in</span>
            </div>
            <div className="  rounded-full bg-orange-100 px-5 text-center text-orange-500">
              <span className=" p-0">
                {new Date().toLocaleString().split(",")[0]}
              </span>
            </div>

            <div className=" flex flex-col">
              <div className=" profile border border-gray-600 rounded-full cursor-pointer relative group">
                <img
                  src={
                    userData.length > 0
                      ? `http://localhost:5000/${userData[0]?.urlpath}`
                      : Avtar
                  }
                  alt="Avatar_image"
                  width="35px"
                  onClick={loginHandler}
                />
                <div
                  className="absolute bottom-0  left-1/2 transform -translate-x-1/2 
                mb-3 px-4 py-2 bg-gray-900 text-white text-sm rounded-md opacity-0 
                group-hover:opacity-100 transition-opacity duration-300"
                >
                  <p>{userData[0]?.username}</p>
                </div>
              </div>
            </div>
          </div>
        </nav>
      </div>
    </>
  );
};

export default NavBar;
